"use client";

import { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Alert, AlertDescription } from "@/components/ui/alert";

interface Position {
  x: number;
  y: number;
}

interface Ship {
  id: string;
  name: string;
  position: Position;
  health: number;
  team: "player" | "enemy";
  selected?: boolean;
  type: "battleship" | "cruiser" | "destroyer" | "scout";
}

interface BattleGridProps {
  onBattleEnd?: (winner: "player" | "enemy") => void;
}

export function BattleGrid({ onBattleEnd }: BattleGridProps) {
  const gridSize = 10;
  const [ships, setShips] = useState<Ship[]>([]);
  const [selectedShip, setSelectedShip] = useState<Ship | null>(null);
  const [battleStarted, setBattleStarted] = useState(false);
  const [battleLog, setBattleLog] = useState<string[]>([]);
  const [isRecording, setIsRecording] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const initialPlayerShips: Ship[] = [
    {
      id: "p1",
      name: "Stellar Vanguard",
      type: "battleship",
      position: { x: 1, y: 8 },
      health: 100,
      team: "player"
    },
    {
      id: "p2",
      name: "Nova Scout",
      type: "scout",
      position: { x: 2, y: 8 },
      health: 100,
      team: "player"
    }
  ];

  const initialEnemyShips: Ship[] = [
    {
      id: "e1",
      name: "Enemy Cruiser",
      type: "cruiser",
      position: { x: 7, y: 1 },
      health: 100,
      team: "enemy"
    },
    {
      id: "e2",
      name: "Enemy Destroyer",
      type: "destroyer",
      position: { x: 8, y: 1 },
      health: 100,
      team: "enemy"
    }
  ];

  const deployFleet = () => {
    try {
      setShips([...initialPlayerShips, ...initialEnemyShips]);
      setBattleStarted(true);
      addToBattleLog("Fleet deployed. Battle commencing...");
    } catch (err) {
      setError("Failed to deploy fleet. Please try again.");
    }
  };

  const handleCellClick = (x: number, y: number) => {
    if (!battleStarted) return;

    // If a ship is selected and the cell is empty, move the ship
    if (selectedShip?.team === "player") {
      const isValidMove = isValidMovePosition(selectedShip, { x, y });
      const isOccupied = ships.some(
        ship => ship.position.x === x && ship.position.y === y
      );

      if (isValidMove && !isOccupied) {
        moveShip(selectedShip.id, x, y);
        simulateEnemyTurn();
      }
    }

    // If no ship is selected, select a ship at this position
    const shipAtPosition = ships.find(
      ship => ship.position.x === x && ship.position.y === y
    );

    if (shipAtPosition?.team === "player") {
      setSelectedShip(shipAtPosition);
      setShips(ships.map(ship => ({
        ...ship,
        selected: ship.id === shipAtPosition.id
      })));
    }
  };

  const isValidMovePosition = (ship: Ship, newPos: Position): boolean => {
    const dx = Math.abs(newPos.x - ship.position.x);
    const dy = Math.abs(newPos.y - ship.position.y);
    
    // Different ship types have different movement ranges
    const maxRange = {
      battleship: 1,
      cruiser: 2,
      destroyer: 2,
      scout: 3
    }[ship.type];

    return dx + dy <= maxRange; // Manhattan distance
  };

  const moveShip = (shipId: string, x: number, y: number) => {
    try {
      const updatedShips = ships.map(ship => {
        if (ship.id === shipId) {
          addToBattleLog(`${ship.name} moved to position (${x}, ${y})`);
          return {
            ...ship,
            position: { x, y },
            selected: false
          };
        }
        return {
          ...ship,
          selected: false
        };
      });
      setShips(updatedShips);
      setSelectedShip(null);
    } catch (err) {
      setError("Failed to move ship. Please try again.");
    }
  };

  const simulateEnemyTurn = () => {
    setTimeout(() => {
      const enemyShips = ships.filter(ship => ship.team === "enemy");
      const playerShips = ships.filter(ship => ship.team === "player");
      
      if (enemyShips.length > 0 && playerShips.length > 0) {
        // Simple AI: Move towards nearest player ship
        const enemyShip = enemyShips[Math.floor(Math.random() * enemyShips.length)];
        const targetShip = playerShips[Math.floor(Math.random() * playerShips.length)];
        
        const dx = targetShip.position.x - enemyShip.position.x;
        const dy = targetShip.position.y - enemyShip.position.y;
        
        const newX = enemyShip.position.x + Math.sign(dx);
        const newY = enemyShip.position.y + Math.sign(dy);
        
        if (newX >= 0 && newX < gridSize && newY >= 0 && newY < gridSize) {
          moveShip(enemyShip.id, newX, newY);
          addToBattleLog(`${enemyShip.name} moves towards ${targetShip.name}`);
        }
      }
    }, 1000);
  };

  const addToBattleLog = (message: string) => {
    setBattleLog(prev => [...prev, `[${new Date().toLocaleTimeString()}] ${message}`]);
  };

  const toggleRecording = () => {
    setIsRecording(!isRecording);
    if (!isRecording) {
      addToBattleLog("Battle recording started");
    } else {
      addToBattleLog("Battle recording stopped");
    }
  };

  const downloadBattleLog = () => {
    try {
      const logText = battleLog.join("\n");
      const blob = new Blob([logText], { type: "text/plain" });
      const url = URL.createObjectURL(blob);
      const a = document.createElement("a");
      a.href = url;
      a.download = "battle-log.txt";
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      URL.revokeObjectURL(url);
    } catch (err) {
      setError("Failed to download battle log");
    }
  };

  return (
    <div className="flex flex-col lg:flex-row gap-6">
      {/* Battle Grid */}
      <div className="flex-grow">
        {error && (
          <Alert variant="destructive" className="mb-4">
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        )}

        <div className="grid grid-cols-10 gap-1 bg-gray-900 p-4 rounded-lg">
          {Array.from({ length: gridSize * gridSize }).map((_, index) => {
            const x = index % gridSize;
            const y = Math.floor(index / gridSize);
            const ship = ships.find(s => s.position.x === x && s.position.y === y);

            return (
              <div
                key={index}
                className={`
                  aspect-square rounded-sm cursor-pointer transition-all
                  ${ship ? (
                    ship.team === "player" 
                      ? "bg-blue-500 hover:bg-blue-400" 
                      : "bg-red-500 hover:bg-red-400"
                  ) : "bg-gray-800 hover:bg-gray-700"}
                  ${ship?.selected ? "ring-2 ring-white" : ""}
                  ${selectedShip && isValidMovePosition(selectedShip, { x, y }) 
                    ? "border-2 border-green-500" 
                    : ""}
                `}
                onClick={() => handleCellClick(x, y)}
                title={ship?.name}
              />
            );
          })}
        </div>

        {/* Battle Controls */}
        <div className="flex gap-2 mt-4">
          <Button
            onClick={deployFleet}
            disabled={battleStarted}
            className="bg-gradient-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700"
          >
            Deploy Fleet
          </Button>
          <Button
            onClick={toggleRecording}
            className={`${
              isRecording 
                ? "bg-red-600 hover:bg-red-700" 
                : "bg-green-600 hover:bg-green-700"
            }`}
          >
            {isRecording ? "Stop Recording" : "Start Recording"}
          </Button>
          <Button
            onClick={downloadBattleLog}
            variant="outline"
            className="border-blue-500 text-blue-500 hover:bg-blue-950"
            disabled={battleLog.length === 0}
          >
            Download Battle Log
          </Button>
        </div>
      </div>

      {/* Battle Log */}
      <Card className="lg:w-80 p-4 bg-gray-900 border-gray-800">
        <h3 className="text-lg font-bold mb-4 text-blue-400">Battle Log</h3>
        <div className="h-[400px] overflow-y-auto space-y-2">
          {battleLog.map((log, index) => (
            <p key={index} className="text-sm text-gray-400">
              {log}
            </p>
          ))}
          {battleLog.length === 0 && (
            <p className="text-sm text-gray-500 italic">
              No battle events recorded yet
            </p>
          )}
        </div>
      </Card>
    </div>
  );
}
