"use client";

import { useState } from "react";
import { BattleGrid } from "@/components/game/BattleGrid";
import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";

interface BattleStats {
  playerShips: number;
  enemyShips: number;
  battleDuration: number;
  damageDealt: number;
}

export default function BattlePage() {
  const [battleStats, setBattleStats] = useState<BattleStats>({
    playerShips: 2,
    enemyShips: 2,
    battleDuration: 0,
    damageDealt: 0
  });

  const handleBattleEnd = (winner: "player" | "enemy") => {
    // Update battle stats when the battle ends
    console.log(`Battle ended. Winner: ${winner}`);
  };

  return (
    <div className="min-h-screen bg-black text-white">
      <div className="max-w-7xl mx-auto px-4 py-8">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-4xl font-bold mb-4 bg-gradient-to-r from-blue-500 to-purple-600 bg-clip-text text-transparent">
            Tactical Battle
          </h1>
          <p className="text-gray-400 mb-6">
            Command your fleet in real-time tactical combat
          </p>
        </div>

        {/* Battle Stats */}
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4 mb-8">
          <StatCard
            title="Your Ships"
            value={battleStats.playerShips}
            total={4}
            color="blue"
          />
          <StatCard
            title="Enemy Ships"
            value={battleStats.enemyShips}
            total={4}
            color="red"
          />
          <StatCard
            title="Battle Duration"
            value={battleStats.battleDuration}
            suffix="min"
            color="purple"
          />
          <StatCard
            title="Damage Dealt"
            value={battleStats.damageDealt}
            suffix="%"
            color="green"
          />
        </div>

        {/* Battle Controls */}
        <div className="flex flex-wrap gap-4 mb-8">
          <Button 
            className="bg-gradient-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700"
          >
            Deploy Fleet
          </Button>
          <Button 
            variant="outline"
            className="border-blue-500 text-blue-500 hover:bg-blue-950"
          >
            Tactical Analysis
          </Button>
          <Button 
            variant="outline"
            className="border-red-500 text-red-500 hover:bg-red-950"
          >
            Retreat
          </Button>
        </div>

        {/* Battle Grid Section */}
        <BattleGrid onBattleEnd={handleBattleEnd} />
      </div>
    </div>
  );
}

interface StatCardProps {
  title: string;
  value: number;
  total?: number;
  suffix?: string;
  color: "blue" | "red" | "purple" | "green";
}

function StatCard({ title, value, total, suffix, color }: StatCardProps) {
  const colorClasses = {
    blue: "from-blue-500 to-blue-700",
    red: "from-red-500 to-red-700",
    purple: "from-purple-500 to-purple-700",
    green: "from-green-500 to-green-700"
  };

  return (
    <Card className="p-4 bg-gray-900 border-gray-800">
      <h3 className="text-sm text-gray-400 mb-2">{title}</h3>
      <div className="flex items-end gap-2">
        <span className="text-2xl font-bold">
          {value}
          {suffix && <span className="text-sm ml-1">{suffix}</span>}
        </span>
        {total && (
          <span className="text-gray-500 mb-1">/ {total}</span>
        )}
      </div>
      {total && (
        <div className="mt-2">
          <div className="h-2 rounded-full bg-gray-800 overflow-hidden">
            <div
              className={`h-full bg-gradient-to-r ${colorClasses[color]} transition-all`}
              style={{ width: `${(value / total) * 100}%` }}
            />
          </div>
        </div>
      )}
    </Card>
  );
}
